@import '../../global.scss';

.contact{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
        flex: 1;
        overflow: hidden;
        img{
            height: 80%;
            
        }
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
        }

        form{
            width: 80%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                 width: 300px;
                 height: 30px;
                 font-size: 14px;
            }

            textarea{
                width: 300px;
                height: 200px;
                font-size: 14px;
            }

            button{
                margin-top: 10px;
                width: 150px;
                height: 30px;
                color: white;
                background-color: $mainColor;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus{
                    outline: none;
                }
            }
        }
    }
}